function columnChart(id){
    let colors = ['#02A0FC',"#34B53A"]
    let options = {
        series: [{
            name: 'Все учителя',
            data: [6, 7, 2.5, 7, 5, 7.5]
        }, {
            name: 'Учителя начальной школы',
            data: [5.8, 5.6, 9, 3, 4.5, 6.5]
        }],
        chart: {
            fontFamily: 'Futura New, Arial, sans-serif',
            type: 'bar',
            height: 220,
            toolbar:{
                show: false
            }
        },
        plotOptions: {
            bar: {
                horizontal: false,
                columnWidth: '55%',
                endingShape: 'rounded'
            },
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            show: true,
            width: 2,
            colors: ['transparent']
        },
        xaxis: {
            categories: ['2017-18', '2018-19', '2019-20', '2020-21', '2021-22', '2022-23'],
            axisTicks: {
                show: false,
            },
            labels: {
                style: {
                    colors: ["#737791"],
                }
            }
        },
        yaxis: {
            title: {
                text: ''
            },
            labels:{
                align: 'left',
                formatter: (value) => {
                    return value + "%"
                },
                style: {
                    colors: ["#737791"],
                }
            }
        },
        fill: {
            opacity: 1,
            colors: colors
        },
        legend:{
            fontSize: '12px',
            markers:{
                fillColors: colors,
                radius: 100,
                width: 8,
                height: 8,
            }
        },
        tooltip: {
            y: {
                formatter: function (val) {
                    return val + " %"
                }
            }
        },
        grid: {
            show: true,
            borderColor: '#EFF1F3',
            xaxis: {
                lines: {
                    show: false
                }
            },
            yaxis: {
                lines: {
                    show: true
                }
            },
        }

    };

    let chart = new ApexCharts(document.querySelector(id), options);
    chart.render();
}


columnChart("#resources-chart")

