function pieChart(id){
    let colors = ['#FF3A29',"#4339F2","#34B53A","#FFB200"]
    let options = {
        series: [31.43,8.64,25.24,34.72],
        labels: [
            'Ниже базового',
            'Базовый',
            'Высокий',
            'Повышенный'
        ],
        chart: {
            fontFamily: 'Futura New, Arial, sans-serif',
            type: 'donut',
            height: 320,
            stacked: true,
            toolbar:{
                show: false
            },
            zoom: {
                enabled: true
            }
        },
        plotOptions: {
            pie: {
                startAngle: -90,
                endAngle: 270
            }
        },
        dataLabels: {
            formatter(val, opts) {
                const name = opts.w.globals.labels[opts.seriesIndex]
                return [val.toFixed(1) + '%']
            }
        },
        stroke: {
            show: true,
            width: 2,
            colors: ['transparent']
        },
        fill: {
            opacity: 1,
            colors: colors
        },
        legend:{
            fontSize: '12px',
            markers:{
                fillColors: colors,
                radius: 100,
                width: 8,
                height: 8,
            },
            position: 'bottom',
            // horizontalAlign: 'left',
        },
        tooltip: {
            y: {
                formatter: function (val) {
                    return val
                }
            }
        },
        grid: {
            show: true,
            borderColor: '#EFF1F3',
            xaxis: {
                lines: {
                    show: false
                }
            },
            yaxis: {
                lines: {
                    show: true
                }
            },
        }

    };

    let chart = new ApexCharts(document.querySelector(id), options);
    chart.render();
}


pieChart("#students-chart2")
